
<template>
<v-row class="pa-6" >
  <v-card width="100%">
    <v-card-title>
      <v-list-item color="rgba(0, 0, 0, .4)" v-if="user">
        <v-list-item-avatar  size="100">
          <v-hover>
            <template v-slot:default="{ hover }">
              <v-img :src="src || '/img/blank-profile-picture.png'" ref="img">
                <v-fade-transition>
                  <v-overlay
                    v-if="hover"
                    absolute
                    color="#036358"
                  >
                    <v-btn icon @click="selectImg">
                      <v-icon>mdi-camera</v-icon>
                    </v-btn>
                  </v-overlay>
                </v-fade-transition>
              </v-img>
            </template>
          </v-hover>
          
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="title">{{user.displayName}}</v-list-item-title>
          <v-list-item-subtitle>{{user.role}}</v-list-item-subtitle>
          <v-list-item-subtitle><v-icon size="15">mdi-map-marker</v-icon> {{user.town}}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-card-title>
    <v-card-text>
      
     
    </v-card-text>
  </v-card>
   
</v-row>
</template>
<script>
 'blank-profile-picture.png'
export default {
  name: 'Profile',
  data: () => ({
    user: null,
    overlay: false,
    src: null
  }),
  methods:{
    selectImg () {
      const file = document.createElement('input')
      file.type = 'file'
      file.accept=".jpg,.jpeg,.png"
      file.onchange = () => {
          const reader = new FileReader();
          reader.addEventListener('load', (event) => {
            this.src = event.target.result
          });
          reader.readAsDataURL(file.files[0]);
      }
      file.click()
    }
  },
  created () {
    this.$store.dispatch('api/get', {
      collection: 'profile'
    }).then(user => {
      this.user = user.data
    })
  }
}
</script>